/* Additional styling */
  



  
h1 {
    font-size: 6.2rem;
    animation: pulsate 0.11s ease-in-out infinite alternate;  
    font-family: "Vibur", sans-serif;   
    
}
  


@keyframes pulsate {
    
  100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #f09,
      0 0 80px #f09,
      0 0 90px #f09,
      0 0 100px #f09,
      0 0 150px #f09;
  
  }
  
  0% {

    text-shadow:
    0 0 4px #fff,
    0 0 10px #fff,
    0 0 18px #fff,
    0 0 38px #f09,
    0 0 73px #f09,
    0 0 80px #f09,
    0 0 94px #f09,
    0 0 140px #f09;

} }
@keyframes pulsate {
    
    100% {
  
        text-shadow:
        0 0 7px #fff,
        0 0 10px #fff,
        0 0 21px #fff,
        0 0 42px blue,
        0 0 82px blue,
        0 0 92px blue,
        0 0 102px blue,
        0 0 151px blue;
    
    }
    
    0% {
  
        text-shadow:
        0 0 7px #fff,
        0 0 8px #fff,
        0 0 19px #fff,
        0 0 40px blue,
        0 0 79px blue,
        0 0 89px blue,
        0 0 99px blue,
        0 0 141px blue;
  
  } 
